import { firebaseConnection } from '../../../firebase';
import store from '../../../store';
export function GetImage(cb) {
    let datasnapshot = firebaseConnection.database().ref('Table1/Image');
    datasnapshot.orderByChild('parent').equalTo(store.getState().element.parentScada).on('value', (images) => {
        let arrData = [];
        let arrDataRun = []
        images.forEach(element => {
            const key = element.key;
            const src = element.val().src;
            const left = element.val().left;
            const top = element.val().top;
            const nameElement = element.val().nameElement;
            const stateElement = element.val().state;
            arrData.push({
                key: key,
                src: src,
                left: left,
                top: top,
                nameElement: nameElement,
                stateElement: stateElement
            });
            if (stateElement) {
                arrDataRun.push({
                    key: key,
                    nameElement: nameElement,
                });
            }
        });
        cb(arrData, arrDataRun);
    })
}
export function GetStatusPLC(cb) {
    let datasnapshot = firebaseConnection.database().ref('Table1/MQTT/STATUS_PLC');
    try {
        datasnapshot.child(store.getState().element.parentScada).on('value', (status) => {
            console.log(status.val())
            if (status.val()){
                cb(status.val().Status);
            }else{
                cb('1');
            }
        })
    } catch (error) {}
}
export function GetPanel(cb) {
    let datasnapshot = firebaseConnection.database().ref('Table1/Panel');
    datasnapshot.orderByChild('parent').equalTo(store.getState().element.parentScada).on('value', (panels) => {
        let arrData = [];
        panels.forEach(element => {
            const key = element.key;
            const width = element.val().width;
            const height = element.val().height;
            const left = element.val().left;
            const top = element.val().top;
            const margin = element.val().margin;
            const backgroundColor = element.val().backgroundColor;
            const padding = element.val().padding;
            const borderRadius = element.val().borderRadius;
            const boxShadow1 = element.val().boxShadow1;
            const boxShadow2 = element.val().boxShadow2;
            const boxShadow3 = element.val().boxShadow3;
            const boxShadowColor = element.val().boxShadowColor;
            const zIndex = element.val().zIndex;
            arrData.push({
                key: key,
                width: width,
                height: height,
                left: left,
                top: top,
                margin: margin,
                backgroundColor: backgroundColor,
                padding: padding,
                borderRadius: borderRadius,
                boxShadow1: boxShadow1,
                boxShadow2: boxShadow2,
                boxShadow3: boxShadow3,
                boxShadowColor: boxShadowColor,
                zIndex: zIndex,
            });
        });
        cb(arrData);
    })
}
